@import "sass/index";

.root {
  background: rgba(255, 255, 255, 0.1);
  width: 100%;
  min-height: 40px;
  padding: 10px 0 10px 20px;
  margin: 5px 0;

  display: flex;
  justify-content: space-between;

  border-radius: 8px;
  font-weight: 500;
}

.text {
  padding: 0;
  margin: 0;
  width: 100%;
  font-size: 1.3rem;
  font-family: "Gilroy";
  color: white;
}
.button {
  font-weight: bold;
  text-align: center;
}
.explanation {
}
.buttonWrapper {
  min-width: 100px;
  border-left: 1px solid white;

  display: flex;
  align-items: center;
}
