@import "sass/index";

$paddingTop: #{8 * (2)}px;
$paddingBottom: #{8 * (2)}px;

.dialogActions {
  padding-top: $paddingTop;
  padding-bottom: $paddingBottom;
  padding-right: spacing(5.5);
  padding-left: spacing(5.5);
}
.title {
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 24px;
  color: use(--template-modal-title-color);
  letter-spacing: 2px;
  text-transform: uppercase;
}
.closeIcon {
  margin-right: spacing(-2);

  & path {
    stroke: use(--template-modal-closeIcon-color);
  }
}
.grow {
  flex: 1 1 auto;
}

:export {
  #{"paddingTop"}: $paddingTop;
  #{"paddingBottom"}: $paddingBottom;
}
