.root {
  height: fit-content;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 22px;
  line-height: 27px;
  color: #ffffff;
  text-transform: uppercase;
  padding: 10px 10px 20px 10px;
}
.closeIcon {
  padding: 0;
  width: 12px;
  height: 12px;
}
